@use "global" as *;

.price__contents {
  @include mq(xl) {
    width: rem(492);
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: rem(0);
  }
}

.price__content {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.price__img {
  display: none;
  @include mq(xl) {
    display: block;
    max-width: rem(492);
    max-height: rem(746);
    margin: rem(24) 0 rem(40) rem(93);
  }
  position: relative;
}

.price__title {
  font-size: rem(16);
  font-family: $base-font-family;
  line-height: rem(24);
  font-weight: $bold;
  border-bottom: 1px solid $blue;
  padding: rem(40) 0 rem(4);
  @include mq(xl) {
    padding: 0;
  }
}

.price__item {
  font-size: rem(14);
  font-family: $base-font-family;
  line-height: rem(24);
  font-weight: $normal;
  padding: rem(8) 0;
}

.price__price {
  font-size: rem(14);
  font-family: $base-font-family;
  line-height: rem(24);
  font-weight: $normal;
  padding: rem(8) 0;
  text-align: right;
}
